.map-swiper {
    position: absolute;
    top: 24px;
    left: 48px;
    z-index: 100;
    opacity: 1;
    transition: 1s ease;
    cursor: pointer;
    
    ul {
        display: flex;
        flex-direction: column;
        gap: 8px;
        .swiper {
            position: relative;
            transition: 0.3s ease;
            font-size: 18px;
            font-weight: bold;
            font-family: Helvetica-Bold;
            color: $cl-dk-body;
            &:first-child {
                &:hover {
                    color: $cl-white;
                }

                &:active {
                    color: $cl-white;
                }
            }
            &:nth-child(2) {
                &:hover {
                    color: $cl-skyblue;
                }
            }
    
            &:nth-child(3) {
                &:hover {
                    color: $cl-pink;
                }
            }
    
            &:nth-child(2) {
                &:active {
                    color: $cl-skyblue;
                }
            }
            &:nth-child(4) {
                &:active {
                    color: $cl-pink;
                }
            }
    
            .marker {
                position: absolute;
                top: 3px;
                left: -36px;
                display: block;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                background: $cl-white;
                opacity: 0;
                transition: 0.3s ease;
            }
    
            .active {
                opacity: 1;
                left: -24px;
            }
        }

        .active {
            color: $cl-white;
        }

        .area-list {
            height: 0;
            max-height: 0;
            margin-left: 8px;
            transition: 0.8s ease;
            overflow: hidden;
            opacity: 0;

            li {
                position: relative;
                left: 0;
                padding: 0 0;
                color: $cl-dk-body;
                font-size: 14px;
                font-family: helvetica-regular;
                transition: 0.3s ease;
                opacity: 0.7;
                &:hover {
                    left: 2px;
                    opacity: 1;
                }
            }

            .list-selected {
                    left: 2px;
                    opacity: 1;
            }
        }

        .area-list-max {
            height: 100%;
            max-height: 400px;
            transition: 0.8s ease;
            opacity: 1;
            padding: 12px 0;
        }
    }
    }


.hide-swiper {
    left: -500px;
    transition: 1s ease;
    overflow: hidden;
}