.user-card {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    gap: 8px;
    padding: 12px 6px;
    border-radius: 6px;
    text-align: center;
    .user-image {
        height: 80px;
    }
    .user-type,
    .user-caption,
    .user-roll {
        color: $cl-dk-body;
    }
}