.spec-block-wrap {
    display: flex;
    gap: 16px;
    .spec-block {
        display: flex;
        flex-direction: column;
        gap: 8px;
        text-align: left;
        .spec-type {
            color: $cl-dk-body;
            opacity: 0.7;
            .spec-caption{
                display: none;
                padding-left: 6px;
                color: $cl-dk-body;
                }
        }
    
        .spec-content {
            padding: 0;
            font-size: 22px;
            color: $cl-dk-heading;
        }
    }
}