.modular-info-chart {
    position: absolute;
    width: 420px;
    bottom: 48px;
    left: 48px;
    z-index: 100;
    opacity: 1;
    transition: 1s ease;

    .return {
        position: absolute;
        top: -20px;
        left: 0;
        display: none;
        position: absolute;
        cursor: pointer;
    }

    .show-return {
        display: block;
    }

    .modular-info-table {
        display: flex;
        flex-direction: column;
        gap: 12px;
        .modular-info-row {
            display: flex;
            align-items: center;

            .info {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 25%;
                height: 36px;
                text-align: center;
                color: $cl-dk-body;
                transition: 0.2s ease;
                opacity: 1;
            }

            .info-title {
                background: rgba(255, 255, 255, 0.2);
                border-radius: 4px;
                cursor: pointer;
            }

            .info-shape {
                position: relative;
                display: flex;
                justify-content: center;
                img {
                    width: 28px;
                    position: absolute;
                    left: calc(50% - 2px);
                }
            }
            .focus-info {
                opacity: 1;
                font-weight: bold;
                color: #191919;
                background: $cl-dk-body;
                border-radius: 4px;
            }
            .hide-info {
                opacity: 0;
            }
        }
    }
}

.hide-modular-info-chart {
    left: -100%;
    opacity: 0;
}