.map-popup-wrap {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -99;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.3s ease;
    background: #909593;
    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.show-map-popup {
    opacity: 1;
    z-index: 99;
}