.video-container {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    opacity: 0;
    transition: 0.3s ease;

    .close-btn {
        position: absolute;
        top: 24px;
        left: 24px;
        z-index: 1;
        width: 48px;
        height: 48px;
    }

    .fin-brand-story-video {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }   
}

.show-video-container {
    opacity: 1;
    z-index: 1000;
}