.info-fac-serve {
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: 100%;
    
    .info-fac-serve-container {
        display: flex;
        flex-direction: column;
        gap: 10px;
        .info-fac-serve-content-wrap {
            display: flex;
            flex-direction: column;
            gap: 12px;
            .info-fac-serve-list {
                display: flex;
                flex-wrap: wrap;
                gap: 6px;

                li {
                    padding: 6px 12px;
                    color: #D5D0C7;
                    background: #676767; 
                    font-size: 14px;
                    line-height: 18px;
                    cursor: pointer;
                    border-radius: 4px;
                    transition: 0.2s ease;
                    &:hover {
                        background: rgba(255,255,255,0.4);
                    }
                }

                .selected {
                    color: #676767;
                    background: #D5D0C7;
                }
            }

            .info-fac-serve-desc {
                .info-desc,
                .info-career {
                    padding: 12px 0;
                    white-space: pre-wrap;
                    .career {
                        width: max-content;
                        text-decoration: underline;
                        margin-right: 4px;
                        color: $cl-body-text;
                        opacity: 0.7;
                        transition: 0.3s ease;

                        &:hover {
                            opacity: 1;
                        }
                    }
                    .copy-body {
                        color: $cl-dk-body;
                    }
                    .copy-body-small {
                        color: $cl-dk-body;
                    }
                }
                
                .hide-desc,
                .hide-career {
                    display: none;
                }
            }
        }

        .info-fac-serve-wrap {
            width: 100%;
            height: auto;
            .info-fac-serve-content {
                display: block;
                width: 100%;
                height: 100%;

                .image-wrap-slide {
                    position: relative;
                    height: auto;
                    overflow: hidden;
                    border-radius: 6px;

                    &:hover {
                        .button {
                            opacity: 1;
                        }
                    }

                    .button {
                        position: absolute;
                        width: 28px;
                        height: 28px;
                        left: 24px;
                        z-index: 15;
                        background: rgba(255,255,255,0.7);
                        border-radius: 50%;
                        cursor: pointer;
                        opacity: 0;
                        transition: 0.3s ease;

                        &:hover {
                            background: $cl-sub-background;
                        }

                        img {
                            position: absolute;
                            left: 7px;
                            width: 14px;
                            height: 14px;
                        }
                    }

                    .button-pre {
                        top: 24px;
                        img {
                            top: 7px;
                        }
                    }

                    .button-next {
                        bottom: 24px;
                        img {
                            top : 8px;
                        }
                    }

                    .hide-button {
                        display: none;
                    }

                    .dot-wrap {
                        display: flex;
                        height: 100%;
                        flex-direction: column;
                        justify-content: center;
                        gap: 6px;
                        position: absolute;
                        left: 36px;
                        z-index: 10;
                        .dot {
                            width: 4px;
                            height: 18px;
                            border-radius: 2px;
                            background: rgba(255,255,255,0.4);
                            cursor: pointer;
                            transition: 0.5s ease;
                            &:hover {
                                background: $cl-sub-background;
                            }
                        }

                        .expand-dot {
                            height: 48px;
                            background: #fff;
                        }
                    }

                    .hide-dot-wrap {
                        display: none;
                    }

                    .image-wrap {
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        height: 50vh;
                        transition: 0.8s ease;
                        background: #191919;
                        .image {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            opacity: 0;
                            transition: 0.3s ease;
                            transform-origin: center center;
                            margin-bottom: 12px;
                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                transition: 0.3s ease;
                            }

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }

                        .show-image {
                            opacity: 1;
                        }
                    }
                }

                .info-fac-serve-content-wrap {
                    padding-top: 24px;
                    .copy-body-small {
                        position: relative;
                        margin-left: 10px;
                        
                        i {
                            display: block;
                            position: absolute;
                            top: 4px;
                            left: -10px;
                            width: 4px;
                            height: 4px;
                            background: $cl-body-text;
                            border-radius: 2px;
                        }
                    }
                }

                .image-front {
                    opacity: 1;
                }

                .image-back {
                    opacity: 0;
                }
            }

            .hide-content {
                display: none;
            }
        }
    }
}