.info-area-context {
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: 100%;
    padding: 24px 0;
    .area-context-content {
        display: flex;
        flex-direction: column;
        gap: 16px;
        .area-context-content-wrap {
            display: flex;
            gap: 18px;
            .context-content {
                display: flex;
                flex-direction: column;
                gap: 8px;
                text-align: left;
                max-width: 30%;
                .copy-body-large {
                    color: $cl-dk-body;
                    .copy-body-small {
                        padding-left: 6px;
                        color: $cl-dk-body;
                        display: none;
                    }
                }
                
                .context-content-list {
                    display: flex;
                    flex-direction: column;
                    gap: 8px;
                    margin-top: 4px;
                    color: $cl-dk-body;
                    opacity: 0.7;

                    .copy-body-small {
                        position: relative;
                        margin-left: 10px;

                        i {
                            display: block;
                            position: absolute;
                            top: 4px;
                            left: -10px;
                            width: 4px;
                            height: 4px;
                            background: $cl-body-text;
                            border-radius: 2px;
                        }
                    }   
                }
            }
        }
        .context-image-wrap {
        }
    }
}