@import './PopupHeader/popup-header.scss';
@import './PopupFacility/popup-facility.scss';
@import './PopupAreaContext/popup-area-context.scss';
@import './PopupUser/popup-user.scss';

@import './PopupHeader/spec-block.scss';
@import './PopupUser/user-profile.scss';

.area-info {
    display: none;
    position: absolute;
    top: 0;
    left: 0;

    .info {
        position: fixed;
        width: 50%;
        height: 100%;
        padding: 36px 80px;
        right: -50%;
        z-index: -10;
        transition: 1s ease-in-out;
        opacity: 0;
        background: linear-gradient(270deg, #2D2C2B 0%, #2D2C2B 94.32%, rgba(45, 44, 43, 0) 100%);
        overflow: scroll;
        cursor: auto;

        -ms-overflow-style: none;
         &::-webkit-scrollbar{
           display:none;
         }

        .close {
            position: absolute;
            top: 36px;
            right: 32px;
            width: 28px;
            height: 28px;
            border-radius: 50%;
            cursor: pointer;
            transition: 0.7s ease;

            &:hover {
                .bar {
                    background: $cl-white;
                }
            }

            .bar {
                display: block;
                position: absolute;
                top: 14px;
                left: 5px;
                width: 17px;
                height: 1px;
                background: $cl-white;
                transition: 0.3s ease;

                &:first-child {
                    transform : rotate(45deg)
                }

                &:last-child {
                    transform: rotate(135deg);
                }
            }
        }

        .expander {
            position: absolute;
            top: 76px;
            right: 32px;
            width: 28px;
            height: 28px;
            cursor: pointer;
            border-radius: 50%;
            transition: 0.3s ease;

            &:hover {
                .bar {
                    &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                    &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                    &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                    &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                    &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                    &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                    &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                    &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                    &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                }
            }

            .bar {
                position: absolute;
                display: block;
                width: 1px;
                height: 1px;
                background: $cl-white;
                transition: 0.3s ease;
                border-radius: 1px;

                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
            }
        }

        .reduction {
            .bar {
                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                
                &:hover {
                    .bar {
                        &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                        &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                        &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                        &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                        &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                        &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                        &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                        &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                        &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
                    }
                }
            }
        }

        .info-content {
            display: flex;
            flex-direction: column;
            gap: 24px;
            .info-sub-title {
                color: $cl-dk-heading;
            }

            .info-body-section {
                width: 100%;
                .info-sub-title {
                    color: $cl-dk-heading;
                }
                
                .hide-info-sub-title {
                    display: none;
                }
            }
        }
    }

    .info-showing {
        right: 0;
        opacity: 1;
        z-index: 900;
        transition: 1s ease-in-out;
    }

    .info-expanding {
        top: 0px;
        right: 0px;
        width: 100%;
        max-width: 100%;
        height: 100%;
        border-radius: 0;
        // width: calc(100% - 96px);
        // height: calc(100% - 48px);
        background: linear-gradient(270deg, #2D2C2B 100%, #2D2C2B 100%, rgba(45, 44, 43, 0) 100%);
        transition: 1s ease;

        .info-content {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;
            
            .info-header-section {
                .info-spec {
                    padding: 0;
                    gap: 12px;
                    .info-spec-wrap {
                        gap: 24px;
                        .info-spec-block {
                            opacity: 0.6;
                            gap: 4px;
                            .copy-body {
                                font-size: 12px;
                            }
                            .copy-body-large {
                                font-size: 16px;
                            }
                        }
                    }
                }
                .info-illust {
                }
            }

            .info-body-section {
                height: 100%;
                .info-fac-serve {
                    width: 100%;
                    border-radius: 8px;
                    height: 100%;
                    .info-fac-serve-container {
                        flex-direction: row;
                        gap: 24px;
                        height: 100%;
                        .info-fac-serve-content-wrap {
                            width: 20%;
                            .info-fac-serve-desc {
                                .info-desc {
                                    max-height: 600px;
                                    overflow-y: scroll;
                                }
                            }
                        }

                        .info-fac-serve-wrap {
                            width: 80%;
                            height: auto;
                            .info-fac-serve-content {
                                height: 100%;
                                .image-wrap-slide {
                                    height: 100%;
                                    .image-wrap {
                                        height: 100%;
                                        .image {
                                            width: 100%;
                                            height: 100%;
                                            img {
                                                width: 100%;
                                                height: 100%;
                                                object-fit: contain;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                .info-area-context {
                    height: 100%;
                    .area-context-content {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        height: 100%;
                        .area-context-content-wrap {
                            .context-content {
                            }
                        }
                        .context-image-wrap {
                            width: 30%;
                            border: 1px solid red;
                        }
                    }
                }

                .info-user {
                    display: none;
                    flex-direction: column;
                    justify-content: space-between;
                    width: 22%;
                    gap: 16px;
                    .focused-user {
                        height: 50%;
                        padding: 22px 16px;
                        background: $cl-sub-background;
                        border-radius: 8px;
    
                        .focused-user-card-list {
                            .focused-user-card {
                                p {
                                    color: $cl-dk-body;
                                }
                            }
                        }
    
                        .focused-user-insight {
                            width: 100%;
                            margin-top: 24px;
                            display: flex;
                            flex-wrap: wrap;
                            .insight {
                                width: 50%;
                                padding: 24px 12px;
                            }
                        }
                    }
                    .related-user {
                        height: 50%;
                        padding: 22px 16px;
                        border-radius: 8px;
                    }
                }
            }
        }
    }
}

.show-area-info {
    display: block;
}