.fin-brand-story-container {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: none;
    white-space: pre-wrap;

    .brand-story-nav-container {
        position: relative;
        display: flex;
        justify-content: center;
        gap: 16px;
        width: 100%;
        padding: 12px 0;
        background: $cl-dk-background;
        
        .close-btn {
            position: absolute;
            width: 36px;
            height: 36px;
            top: calc(50% - 18px);
            right: 12px;
            z-index: 10;
        }

        .brand-story-numbering {
            display: flex;
            justify-content: center;
            align-items: flex-end;
            gap: 16px;
            .btn {
                width: 24px;
                height: 24px;
                opacity: 0.5;
                transition: 0.3s ease;
                cursor: pointer;
                &:hover {
                    opacity: 1;
                }
            }
            .num {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                position: relative;
                gap: 4px;
                padding-bottom: 2px;
                opacity: 0.5;
                color: $cl-main;
                transition: 0.3s ease;
                cursor: pointer;

                .marker {
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: $cl-main;
                    transition: 0.3s ease;
                    opacity: 0;
                }
                .show-marker {
                    opacity: 0.8;
                }
            }
            .select-num {
                opacity: 0.8;
                font-weight: bold;
            }
        }
    }

    .brand-story-content-wrap {
        position: relative;
        height: 100%;
        background: $cl-dk-background;
        .brand-story-content {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
            opacity: 0;
            transition: 0.3s ease;

            .brand-story-text-wrap {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                display: flex;
                flex-direction: column;
                justify-content: center;
                gap: 12px;
                padding: 5% 3.75%;
                width: 100%;
                height: 100%;
                color: $cl-dk-heading;
                .content-caption {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;
                    position: absolute;
                    top: 36px;
                    .caption {
                        opacity: 0.7;
                    }
                    .caption-heading {
                        font-size: 24px;
                        opacity: 1;
                    }
                    .caption-body {
                        opacity: 0.7;
                    }
                }
                .content-heading {
                    font-size: 48px;
                    line-height: 60px;
                    width: 50%;
                    color: $cl-dk-heading;
                }

                .content-body {
                    width: 50%;
                }

                .content-image {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    gap: 24px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    img {
                        width: 240px;
                    }
                    p {
                        color: $cl-white;
                        opacity: 0.7;
                    }
                }
            }
            .image-video-wrap {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 0;
                width: 100%;
                height: 100%;
                .brand-story-image {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

                .fin-brand-story-video {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
        .show-brand-story-content {
            opacity: 1;
            z-index: 100;
        }
    }
}

.show-brand-story-container {
    display: flex;
}