.masterplan-fin-container {
    opacity: 0;
    position: absolute;
    top: calc(50% - $map-half-height);
    left: calc(50% + 170px);
    width: 600px;
    height: $map-height;
    z-index: 10;
    transition: 1s ease;

    .masterplan-cabin {
        position: absolute;
        top: calc(50% - $map-half-height);
        left: calc(50% - 300px);
        width: 600px;
        height: $map-height;
        transition: 0.3s ease;
        opacity: 0;
        .masterplan-cabin-img {
            position: absolute;
            top: 0;
            opacity: 0.2;
            transition: 0.3s ease;
        }
        .masterplan-cabin-ab-img {
            top: 131px;
            left: 264px;
            width: 215px;
            height: 452px;
        }
        .masterplan-cabin-cd-img {
            top: 165px;
            left: 46px;
            width: 359px;
            height: 597px;
        }
        .masterplan-cabin-ef-img {
            top: 21px;
            left: 306px;
            width: 256px;
            height: 684px;
        }
        .focus-masterplan-cabin-img {
            opacity: 1;
        }
        .all-masterplan-cabin-img {
            opacity: 1;
        }
    }

    .show-masterplan-cabin {
        opacity: 1;
    }

    .pin-wrap {
        .pin-box {
            position: absolute;
            &:hover {
                .area-mark {
                    opacity: 0.7;
                    transition: 0.3s ease;
                }
            }
            .area-mark {
                opacity: 0;
                transition: 0.3s ease;
                cursor: pointer;
                &:hover ~ .pin {
                    background: orange;
                }
            }
    
            .pin {
                position: absolute;
                z-index: 50;            
                display: flex;
                align-items: center;
                transition: 0.5s ease;
                cursor: pointer;
    
                &:hover {
                    .pin-title,
                    .pin-icon,
                    .pin-line {
                        opacity: 1;
                    }
                }
                
                .pin-head {
                    width: 8px;
                    height: 8px;
                    top: 1px;
                    display: flex;
                    justify-content: center;
                    border-radius: 50%;
                    transition: 0.3s ease;
                    font-size: 10px;
                    font-family: Helvetica-Bold;
                    line-height: 22px;
                    background: orange;
                    color: $cl-black;
                    transition: 0.3s ease;
                }
    
                .pin-title {
                    opacity: 0;
                    bottom : 2px;
                    width: max-content;
                    padding: 0 6px;
                    font-size: 10px;
                    line-height: 14px;
                    color: $cl-white;
                    transition: 0.3s ease;
                }
                
                .pin-icon {
                    opacity: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 36px;
                    height: 36px;
                    border: 0.4px solid $cl-white;
                    border-radius: 50%;
                    background: $cl-dk-background;
                    transition: 0.3s ease;
    
                    img {
                        width: 24px;
                        height: 24px;
                    }
                }
    
                .pin-line {
                    opacity: 0;
                    height: 0.2px;
                    background: $cl-white;
                    transition: 0.3s ease;
                }
    
                .show-pin-head {
                    background: $cl-white;
                }
    
                .show-pin-head,
                .show-pin-line,
                .show-pin-icon,
                .show-pin-title {
                    transition: 0.3s ease;
                    opacity: 0.6;
                }
            }
    
            .hide-pin {
                opacity: 0;
    
                &:hover {
                    opacity: 0;
                }
            }
        }
    }

    .fin-pin-wrap {
        .fin-pin-selected {
            .area-mark {
                opacity: 0.7;
            }
        }

        .fin-pin {
            // premium area
            &:nth-child(1) { 
                top: 38px;
                left: 363.05px;
                width: 52px;
                height: 92px;
                .pin {
                    top: 6px;
                    left: 21px;
                    .pin-line {
                        width: 254px;
                    }
                }
            }
    
            // figga
            &:nth-child(2) { 
