@import './fin-pins.scss';
@import './hills-pins.scss';
@import './zoning.scss';

.map-container {
    width: 100%;
    height: 100%;
    position : relative;

    .masterplan {
        transition: 1s ease;
        .background-map {
            position: absolute;
            top: calc(50% - $map-half-height);
            left : calc(50% - $map-half-width);
            width: $map-width;
            height: $map-height;
            overflow: hidden;
            opacity: 1;
            transition: 1s ease;
    
            img {
                position: absolute;
                left: 0;
                width: $map-width;
                height: $map-height;
                transition: 1s ease;
            }
        }
    }
    
    .masterplan-fin {
        .masterplan-fin-container {
            width: 600px;
            left: calc(50% - 300px);
            opacity: 1;
            z-index: 10;
            overflow: visible;
            transition: 1s ease;
        }

        .move-left-fin-container {
            left: calc(25% - 300px);
        }

        .s-fin-fac-pin-wrap {
            z-index: 9;
            .s-fin-fac-pin {
                .pin-headline {
                    opacity: 1;
                }
            }
        }

        .s-hills-pin-wrap,
        .s-hills-fac-pin-wrap {
            width: 0px;
            overflow: hidden;
        }

        .background-map {
            width: 600px;
            top: calc(50% - $map-half-height);
            left : calc(50% - 300px);
            opacity: 0.7;
            img {
                left: calc(600px - $map-width);
            }
        }

        .move-background-map {
            left: calc(25% - 300px);
        }
    }

    .masterplan-hills {
        .s-hills-pin-wrap,
        .s-hills-fac-pin-wrap {
            width: 1100px;
            left: calc(50% - 550px);
            opacity: 1;
            overflow: visible;
            .s-hills-pin {
                .pin-headline {
                    opacity: 1;
                }
            }
        }

        .s-hill-fac-pin-wrap {
            z-index: 9;
            .s-hill-fac-pin {
                .pin {
                    opacity: 1;
                }
            }
        }

        .s-fin-pin-wrap,
        .s-fin-fac-pin-wrap {
            width: 0px;
            overflow: hidden;
        }

        .background-map {
            width: 1100px;
            top: calc(50% - $map-half-height);
            left : calc(50% - 550px);
            opacity: 0.7;
            img {
                left: 0;
            }
        }
    }

    .layer {
        position: absolute;
        top: calc(50% - $map-half-height);
        z-index: 5;
        height: $map-height;
        transition: 1s ease;
        width: 0;
        opacity: 1;
    }

    .layer-left {
        left: calc(50% - $map-half-width);
        background: linear-gradient(to right, rgba(45, 45, 45, 1) 0%, rgba(45, 45, 45, 0) 10%);
    }

    .layer-right {
        left: calc(50% + $map-half-width);
        background: linear-gradient(to left, rgba(45, 45, 45, 1) 8.34%, rgba(45, 45, 45, 0) 20.18%);
    }

    .show-layer-left {
        left: calc(50% - 300px);
        width: 600px;
        opacity: 1;
    }
    .move-layer-left {
        left: calc(25% - 300px);
    }
    .show-layer-right {
        left: calc(50% - 550px);
        width: 1100px;
        opacity: 1;
    }
}