.info-user {
    width: 100%;

    .focused-user {
        padding: 24px 0;
        .focused-user-card-list {
            display: flex;
            flex-wrap: wrap;
            gap: 12px;
            .focused-user-card {
                p {
                    color: $cl-dk-body;
                }
            }
        }
        .focused-user-insight {
            display: none;
            .insight {
                padding: 8px 0;
                .copy-body-small {
                    position: relative;
                    margin-left : 10px;
                    color: $cl-dk-body;

                    i {
                        display: block;
                        position: absolute;
                        top: 4px;
                        left: -10px;
                        width: 4px;
                        height: 4px;
                        background: $cl-dk-body;
                        border-radius: 2px;
                    }
                }
            }
        }
    }

    .related-user {
        padding: 24px 0;
        .related-user-card-list {
            display: flex;
            flex-wrap: wrap;
            gap: 12px;
            .related-user-card {
                p {
                    color: $cl-dk-body;
                }
            }
        }
    }
}