$cl-main : #685F5F;
$cl-background : #F0EBE0;
$cl-sub-background : #E9E7DF;
$cl-body-text : #676767;

$cl-lt-heading : #685F5F;
$cl-lt-background : #F0EBE0;
$cl-lt-sub-background : #E9E7DF;
$cl-lt-body : #676767;

$cl-dk-background : #2D2D2D;
$cl-dk-heading : #F0EBE0;
$cl-dk-body : #D4D0C7;
$cl-skyblue : #ABC6FF;
$cl-pink : #DB93CB;

$cl-white : #FFFFFF;
$cl-black : #191919;
$map-width : 1540px;
$map-height : 800px;
$map-half-width : calc($map-width / 2);
$map-half-height : calc($map-height / 2);

@font-face {
    font-family: Helvetica-Bold;
    src: url("./assets/font/HelveticaNeueLTPro-BdCn.otf") format("opentype");
}

@font-face {
    font-family: Helvetica-Regular;
    font-weight: bold;
    src: url("./assets/font/HelveticaNeueLTPro-Md.otf") format("opentype");
}

.th-dk-bg { background-color: $cl-dk-background; transition: 0.3s ease;}
.th-dk-pointer-bg { background-color: $cl-white; transition: 0.3s ease;}
.th-dk-heading { color: $cl-dk-heading; transition: 0.3s ease;}
.th-dk-body { color: $cl-dk-body; transition: 0.3s ease;}

.th-lt-bg { color: $cl-lt-background; transition: 0.3s ease;}
.th-lt-pointer-bg { background-color: $cl-black; transition: 0.3s ease;}
.th-lt-heading { color: $cl-lt-heading; transition: 0.3s ease;}
.th-lt-body { color: $cl-lt-body; transition: 0.3s ease;}

@mixin font-heading {
    display: flex;
    align-items: flex-end;
    font-size: 32px;
    line-height: 42px;
    font-weight: bold;
    font-family: Helvetica-Bold, sans-serif;
}

@mixin font-sub-heading {
    display: flex;
    align-items: center;
    font-size: 24px;
    line-height: 36px;
    font-weight: bold;
    font-family: Helvetica-Bold, sans-serif;
}

@mixin font-body-large {
    font-size: 16px;
    font-weight: bold;
    line-height: 22px;
    font-family: Helvetica-Regular, sans-serif;
}

@mixin font-body-regular {
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
}

@mixin font-body-small {
    display: inline-block;
    font-size: 12px;
    line-height: 16px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
}

@mixin font-caption {
    display: inline-block;
    font-size: 10px;
    line-height: 14px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
}

.copy-heading { @include font-heading; }
.copy-sub-heading { @include font-sub-heading; }
.copy-body-large { @include font-body-large; }
.copy-body { @include font-body-regular; }
.copy-body-small { @include font-body-small; }
.copy-caption { @include font-caption; }