.s-hills-pin-wrap {
    opacity: 0;
    position: absolute;
    top: calc(50% - 480px + 167px);
    left: calc(50% - $map-half-width);
    width: $map-half-width;
    height: 480px;
    z-index: 10;
    transition: 1s ease;

    .s-hills-pin {
        position: absolute;
        &:hover {
            .area-mark {
                opacity: 1;
                transition: 0.3s ease;
            }
        }
        .area-mark {
            opacity: 0;
            transition: 0.3s ease;
            cursor: pointer;
            &:hover ~ .pin {
                background: orange;
            }
        }

        .pin {
            position: absolute;
            z-index: 50;            
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            transition: 0.5s ease;
            cursor: pointer;

            &:hover {
                .pin-title,
                .pin-icon,
                .pin-line {
                    opacity: 1;
                }
            }
            
            .pin-head {
                width: 8px;
                height: 8px;
                top: 1px;
                display: flex;
                justify-content: center;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 22px;
                background: $cl-white;
                color: $cl-black;
                transition: 0.3s ease;
                background: orange;
            }

            .pin-title {
                opacity: 0;
                bottom : 2px;
                width: max-content;
                padding: 6px 0px;
                font-size: 10px;
                line-height: 14px;
                color: $cl-white;
                transition: 0.3s ease;
            }
            
            .pin-icon {
                opacity: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 36px;
                height: 36px;
                border: 1px solid $cl-white;
                border-radius: 50%;
                background: $cl-dk-background;
                transition: 0.3s ease;

                img {
                    width: 24px;
                    height: 24px;
                }
            }

            .pin-line {
                opacity: 0;
                width: 0.2px;
                background: $cl-white;
                transition: 0.3s ease;
            }

            .show-pin-head {
                background: white;
            }

            .show-pin-head,
            .show-pin-line,
            .show-pin-icon,
            .show-pin-title {
                transition: 0.3s ease;
                opacity: 0.6;
            }
        }

        .hide-pin {
            opacity: 0;

            &:hover {
                opacity: 0;
            }
        }


        // chapel
        &:nth-child(1) { 
            top: 74px;
            left: 508px;
            width: 29px;
            height: 29px;
            .pin {
                left: -20px;
                top: -190px;
                flex-direction: column-reverse;
                .pin-line {
                    height: 130px;
                }
            }
        }

        // bakery
        &:nth-child(2) { 
            top: 76px;
            left: 564px;
            width: 46px;
            height: 46px;
            .pin {
                left: 14px;
                top: 32px;
                .pin-line {
                    height: 374px;
                }
            }
        }
        // treehouse
        &:nth-child(3) {
            top: 135px; 
            left: 453px;
            width: 345px;
            height: 162px;
            .pin {
                top: 60px;
                left: -50px;
                .pin-line {
                    height: 285px;
                }
            }
        }
    }

    .s-hills-pin-selected {
        .pin {
                .pin-head {
                    color: #191919;
                }
        }
        .area-mark {
            opacity: 1;
        }
    }
}

