.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 48px;
    width: 100%;
    height: 100vh;
    background: $cl-dk-background;
    transition: 2s ease-out;
    opacity: 1;
    cursor: auto;

    .br-logo {
        width: 224px;
        height: 224px;
        cursor: pointer;
        transition: 0.3s ease;
        &:hover {
            opacity: 0.7;
        }
    }

    .br-title {
        color: $cl-white;
        opacity: 0.7;
        font-weight: lighter;
        font-size: 18px;
        letter-spacing: 12px;
        padding-left: 14px;
    }
    
    .br-admin {
        display: flex;
        flex-direction: column;
        align-items: center;

        input, button {
            text-align: center;
            background: none;
            outline: none;
            border: none;
            color: white;
        }

        input {
            border-bottom: 1px solid rgba(255, 255, 255, 0.4);
            margin-bottom: 12px;
        }

        .warning {
            font-size: 12px;
            padding: 12px;
            color: red;
            opacity: 0.4;
            display: none;
        }

        .error {
            display: block;
        }

        button {
            transition: 0.3s ease;
            font-size: 12px;
            cursor: pointer;
            a {
                color: #fff;
                cursor: pointer;
            }
            &:hover {
                opacity: 0.5;
            }
        }
    }
}

.roll-off-intro {
    z-index: -10;
    opacity: 0;
}